<template>
  <div id="drawer">
      <div class="npc-card">
      <div
        style="order: 2; display: flex; width: 60%;"
        v-if="images[index]">

        <div style="margin: auto 0;">
           <h3 style="width: 100%; text-align: center;">Image {{index + 1}}</h3>
          <img
            @click="createLabel"
            ref="imageDetector"
            class="imageDetector"
            :src="'http://localhost:8095/getImage/' + images[index]"
          />

          <Box v-if="creatingLabel"
        :top="creatingBox.top" :left="creatingBox.left"
        :width="creatingBox.width" :height="creatingBox.height"
        :label="creatingBox.label" :color="creatingBox.color"
        />
      <div v-for="(box, id) in boxes[index]"
                 :key="id"
                 style="z-index: 1000;"
      >
      <Box :top="box.top" :left="box.left"
        :width="box.width" :height="box.height"
        :label="box.label"
        @click="removeBox(index, id)"
        />
      </div>

        </div>
      </div>
      <section>
        <h3>Manual drawer</h3>
        <separator width="190px" backgroundColor="black" height="2px" />
        <p>
          <b>Manually draw detection lines</b>
          <br>
          <br>
           Select a directory where your images is stored, the program will open all the images and let you draw rectangles to mark objects for image recognition.
          <br>
          <br>
           Images will be processes and saved to a subfolder in the same directory. Rectangles are drawed for the YOLOv3 engine.
          <br>
          <br>
          </p>
           <div class="inline">

            <div style="display: flex; flex-direction: column; width: 50%; ">
              <b>Name of project:</b>
              <input
                style="height: 30px;margin-top: 5px;"
                placeholder="carLabels"
                v-model="projectName"
                />
              </div>
              <div style="display: flex; flex-direction: column; margin-left: 20px;">
                <b>Allowed filetypes:</b>
                <div class="inline">
                  <el-checkbox
                  style="margin-right: 10px; margin-top: 5px;"
                  :options="[
                  {
                    value: true,
                    label: 'Images'
                  }]"
                  v-model="allowImages"
                  />
                  <el-checkbox
                  style="margin-right: 10px; margin-top: 5px;"
                  :options="[
                    {
                    value: true,
                    label: 'Videos'
                  }]"
                  v-model="allowVideos"
                  />
              </div>
            </div>
               <div style="display: flex; flex-direction: column; margin-left: 20px;">
                 <div style="display: block; position: relative; width: 100%;">
                  <b>Limit the maximum images loaded</b>
                  <el-tooltip>
                    If you have a huge scope, or slow computer. Limit the images in scope. <br> Each image is only the name, not entire image (not implemented yet)
                  </el-tooltip>
                </div>
                <div class="inline">
                  <el-checkbox
                  style="margin-right: 10px; margin-top: 5px;"
                  :options="[
                  {
                    value: true,
                    label: 'Max images'
                  }]"
                  v-model="maxImagesCheck"
                  />
              </div>
            </div>
          </div>

          <div
            v-if="this.maxImagesCheck[0] === true">
               <b>Maximum images in scope</b>
              <input
                style="height: 30px;margin-top: 5px;"
                placeholder="1000"
                type="number"
                v-model="maxImages"
                />
          </div>

           <div
            v-if="this.allowVideos[0] === true">
               <b>Delay between images from video (1000 is 1 second, 0 is every frame)</b>
              <input
                style="height: 30px;margin-top: 5px;"
                placeholder="1000"
                type="number"
                v-model="frameDelay"
                />
          </div>

          <div style="display: block; position: relative; width: 100%;">
            <b>Image directory: (?)</b>
            <el-tooltip>
              Where the images/videos are stored. Give an absolute path and not a relative. <br> This path is then read by the server and all images copied <br> to the servers /data/${projectName}/ folder.
            </el-tooltip>
          </div>

          <div class="inline">
          <input
            style="height: 30px; width: 50%;"
            placeholder="/data/passages"
            v-model="src"
            />
            <el-button @click="searchImages" style="margin-left: 10px;">Search</el-button>
          </div>
           <div style="display: block; position: relative; width: 100%;">
            <b>Custom shortcuts for different detection objects, add them below. (?)</b>
            <el-tooltip>
              Shortcuts for handling images. Default shortcuts are enter, <br> right-arrow for next image and back, left-arrow for previous image.
            </el-tooltip>
          </div>
          <div class="inline">
          <input
            style="height: 30px; width: 30%;"
            placeholder="key"
            v-model="shortcut.key"
            />
            <input
            style="height: 30px; width: 30%; margin-left: 2px;"
            placeholder="model"
            v-model="shortcut.model"
            />
            <el-button secondary @click="addShortcut" style="margin-left: 10px;">Add</el-button>
          </div>
          <table id="shortcutTable" style="border: 1px solid #f0f0f0;">
            <thead>
              <tr>
                <th>Key</th>
                <th>Model</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="shortcut in shortcut.shortcuts"
                 :key="shortcut.key"
                style="margin-top:5px;">
                <td>{{shortcut.key}}</td>
                <td>{{shortcut.model}}</td>
                <td><el-button danger secondary @click="deleteShortcut(shortcut)">Radera</el-button></td>
              </tr>
            </tbody>
          </table>
        <div style="text-aling: center; margin: 30px auto -15px auto; font-size: 20px;">
          <b>{{index}}</b> out of <b>{{total}}</b> images processed
        </div>

        <br>
        <br>
        <div class="inline" style="justify-content: center;">
         <el-button @click="saveProgress" style="height: 45px; font-size: 20px; margin-right: 10px;">Save progress</el-button>
        <el-button @click="loadProgress" secondary style="height: 45px; font-size: 20px;">Load progress</el-button>
        </div>
        <p
          style="margin-top:10px;"
          v-if="drawerFlag.state !== 0"
          :class="{
            errorMessage: drawerFlag.state === 2,
            confirmMessage: drawerFlag.state === 1,
            loadingMessage: drawerFlag.state === 3
          }"
        >
          <font-awesome-icon
            :icon="[
              'fas',
              drawerFlag.state === 2
                ? 'exclamation-triangle'
                : drawerFlag.state === 1
                ? 'check-circle'
                : 'spinner'
            ]"
            class="icon"
            :class="drawerFlag.state === 3 ? 'fa-spin' : null"
          ></font-awesome-icon>
          {{ drawerFlag.msg }}
        </p>
      </section>
    </div>
  </div>
</template>

<script>
import Separator from './../components/Separator.vue'
import Box from './../components/Box.vue'
import logFile from './../script/log'
import drawerHandler from './../services/drawerHandler'

const log = logFile('Drawer')

export default {
  components: {
    Box,
    Separator
  },
  data () {
    return {
      mouse: {
        x: 0,
        y: 0
      },
      creatingLabel: false,
      creatingBox: {
        top: 600,
        left: 900,
        height: 50,
        width: 30,
        color: 'orange',
        label: ''
      },
      boxes: [],
      shortcut: {
        key: '',
        model: '',
        shortcuts: [{
          key: 'enter',
          model: 'Next image'
        }, {
          key: 'back',
          model: 'Previous image'
        }, {
          key: 'escape',
          model: ''
        }, {
          key: 'a',
          model: 'Outer road mark line'
        }, {
          key: 's',
          model: 'Center road mark line'
        }, {
          key: 'd',
          model: 'truck'
        }]
      },

      maxImages: 0,
      right: true,
      isAsset: true,
      projectName: 'DATX02',
      src: '/Users/jesper/Projects/JNSystems/Python/DATX02-Vehicle-Automation/computerVision/manualDetection/data',
      images: [],
      total: 0,
      index: 0,
      frameDelay: 500,
      allowImages: [true],
      allowVideos: [true],
      maxImagesCheck: [false],
      drawerFlag: {
        state: 0,
        msg: ''
      }
    }
  },
  props: {

  },
  mounted () {
    this.$store.commit('resetLoading')
    window.addEventListener('keydown', e => {
      this.checkListeners(String.fromCharCode(e.keyCode), e.keyCode)
    })
    window.addEventListener('mousemove', e => {
      this.trackmouse(e)
    })
    this.searchImages()
  },
  computed: {

  },
  methods: {
    async searchImages  () {
      this.setFlag(3, 'Loading images')
      this.$store.commit('setLoading')
      try {
        if (this.src === null || this.src === '') {
          this.$store.commit('resetLoading')
          this.setFlag(2, 'Path to image diretory not found')
          return
        }
        const allowImages = this.allowImages[0] === true
        const allowVideos = this.allowVideos[0] === true

        const searchImagesArgs = {
          projectName: this.projectName,
          imageSrc: this.src,
          frameDelay: this.frameDelay,
          allowImages,
          allowVideos,
          maxImages: this.maxImages
        }
        const response = await drawerHandler.searchImages(searchImagesArgs)

        this.total = response.total
        this.images = response.images
        this.setFlag(1, 'Images loaded')
        this.$store.commit('resetLoading')
      } catch (err) {
        log.error(err)
        this.$store.commit('resetLoading')
        this.setFlag(2, err.message)
      }
    },
    setFlag (state, msg) {
      this.drawerFlag.state = state
      this.drawerFlag.msg = msg

      setTimeout(() => {
        this.drawerFlag.state = 0
      }, 3000)
    },
    addShortcut () {
      if (this.shortcut.key === undefined || this.shortcut.key === '') {
        this.setFlag(2, 'Shortcut key is unset')
        return
      }
      if (this.shortcut.model === undefined || this.shortcut.model === '') {
        this.setFlag(2, 'Shortcut model is unset')
        return
      }
      this.shortcut.shortcuts.push({
        key: this.shortcut.key,
        model: this.shortcut.model
      })
    },
    deleteShortcut (shortcut) {
      this.shortcut.shortcuts.forEach((sc, index) => {
        if (sc.key === shortcut.key) {
          this.shortcut.shortcuts.splice(index, 1)
        }
      })
    },
    changeImage (index) {
      this.index += index
      if (this.index > this.images.length - 1) {
        this.index = this.images.length - 1
      }
      if (this.index < 0) {
        this.index = 0
      }
    },
    removeBox (index, id) {
      const temp = this.boxes
      temp[index].splice(id, 1)
      this.boxes = []
      this.boxes = temp
    },
    createLabel (event, label) {
      const mouse = this.getCoordinates(event)
      const image = this.getImagePosition()
      if (image === undefined) return

      let top = (image.y1 + mouse.y)
      let left = (image.x1 + mouse.x)
      label = label || ''
      if (this.creatingLabel) {
        this.creatingLabel = false
        label = this.creatingBox.label
        let oldLeft = this.creatingBox.left
        let oldTop = this.creatingBox.top
        if (left < oldLeft) {
          let tmpLeft = left
          left = oldLeft
          oldLeft = tmpLeft
        }
        if (top < oldTop) {
          let tmpTop = top
          top = oldTop
          oldTop = tmpTop
        }

        this.addLabel(label, oldLeft, oldTop, left, top)
      } else {
        this.startNewLabel('', top, left)
      }
    },
    updateCreatingLabel () {
      const image = this.getImagePosition()
      if (image === undefined) return

      let top = (image.y1 + this.mouse.y)
      let left = (image.x1 + this.mouse.x)

      let oldLeft = this.creatingBox.left
      let oldTop = this.creatingBox.top

      let newLeft = oldLeft
      let newTop = oldTop
      if (left < oldLeft) {
        newLeft -= oldLeft - left
        let tmpLeft = left
        left = oldLeft
        oldLeft = tmpLeft + oldLeft - left
      }
      if (top < oldTop) {
        newTop -= oldTop - top
        let tmpTop = top
        top = oldTop
        oldTop = tmpTop + oldTop - top
      }

      this.creatingBox.top = newTop
      this.creatingBox.left = newLeft
      this.creatingBox.height = top - oldTop
      this.creatingBox.width = left - oldLeft
    },
    startNewLabel (label, top, left) {
      this.creatingBox.top = top
      this.creatingBox.left = left
      this.creatingBox.height = 0
      this.creatingBox.width = 0
      this.creatingBox.label = label

      this.creatingLabel = true
    },
    addLabel (label, x1, y1, x2, y2) {
      if (this.boxes[this.index] === undefined) {
        this.boxes[this.index] = []
      }
      this.boxes[this.index].push({
        top: y1,
        left: x1,
        height: y2 - y1,
        width: x2 - x1,
        color: 'red',
        label: label
      })
    },
    checkListeners (char, keyCode) {
      if (keyCode === 13) char = 'enter'
      if (keyCode === 39) char = 'enter'
      if (keyCode === 8) char = 'back'
      if (keyCode === 37) char = 'back'
      if (keyCode === 27) char = 'escape'
      this.shortcut.shortcuts.forEach(sc => {
        if (sc.key.toLowerCase() === char.toLowerCase()) {
          if (sc.model.toLowerCase() === 'next image') {
            this.changeImage(1)
          } else if (sc.model.toLowerCase() === 'previous image') {
            this.changeImage(-1)
          } else if (sc.model.toLowerCase() === 'escape') {
            this.creatingLabel = false

            setTimeout(() => {
              this.creatingLabel = false
            }, 50)
          } else {
            const image = this.getImagePosition()
            if (image === undefined) return
            let top = (image.y1 + this.mouse.y)
            let left = (image.x1 + this.mouse.x)
            this.startNewLabel(sc.model, top, left)
          }
        }
      })
    },
    findPosition (oElement) {
      if (oElement !== undefined && typeof (oElement.offsetParent) !== 'undefined') {
        for (var posX = 0, posY = 0; oElement; oElement = oElement.offsetParent) {
          posX += oElement.offsetLeft
          posY += oElement.offsetTop
        }
        return [ posX, posY ]
      } else if (oElement !== undefined) {
        return [ oElement.x, oElement.y ]
      } else {
        return [0, 0]
      }
    },
    getCoordinates (e) {
      var PosX = 0
      var PosY = 0
      var ImgPos
      ImgPos = this.findPosition(this.$refs.imageDetector)
      if (!e) e = window.event
      if (e.pageX || e.pageY) {
        PosX = e.pageX
        PosY = e.pageY
      } else if (e.clientX || e.clientY) {
        PosX = e.clientX + document.body.scrollLeft +
        document.documentElement.scrollLeft
        PosY = e.clientY + document.body.scrollTop +
        document.documentElement.scrollTop
      }
      PosX = PosX - ImgPos[0]
      PosY = PosY - ImgPos[1]

      return { x: PosX, y: PosY }
    },
    getImagePosition () {
      const img = this.$refs.imageDetector
      if (img === undefined) return
      return {
        x1: img.getBoundingClientRect().left,
        y1: img.getBoundingClientRect().top,
        x2: img.getBoundingClientRect().right,
        y2: img.getBoundingClientRect().bottom,
        h: img.height,
        w: img.width
      }
    },
    trackmouse (event) {
      this.mouse = this.getCoordinates(event)
      if (this.creatingLabel) {
        this.updateCreatingLabel()
      }
    },
    async saveProgress () {
      this.setFlag(3, 'Saving progress')
      this.$store.commit('setLoading')
      try {
        if (this.src === null || this.src === '') {
          this.$store.commit('resetLoading')
          this.setFlag(2, 'Path to image diretory not found')
          return
        }
        const allowImages = this.allowImages[0] === true
        const allowVideos = this.allowVideos[0] === true

        const args = {
          projectName: this.projectName,
          imageSrc: this.src,
          frameDelay: this.frameDelay,
          allowImages,
          allowVideos,
          maxImages: this.maxImages,
          boxes: this.boxes,
          shortcuts: this.shortcuts,
          images: this.images,
          total: this.total,
          index: this.index
        }
        await drawerHandler.saveProgress(args)
        this.setFlag(1, 'Progress saved')
        this.$store.commit('resetLoading')
      } catch (err) {
        log.error(err)
        this.$store.commit('resetLoading')
        this.setFlag(2, err.message)
      }
    },
    async loadProgress () {
      this.setFlag(3, 'Loading progress')
      this.$store.commit('setLoading')
      try {
        const res = await drawerHandler.loadProgress(this.projectName)
        this.src = res.imageSrc
        this.frameDelay = Number(res.frameDelay)
        this.maxImages = Number(res.maxImages)
        this.boxes = res.boxes
        this.shortcuts = res.shortcuts
        this.images = res.images
        this.total = Number(res.total)
        this.index = Number(res.index)
        res.allowImages ? this.allowImages[0] = true : this.allowImages[0] = false
        res.allowVideos ? this.allowVideos[0] = true : this.allowVideos[0] = false

        this.creatingLabel = false
        this.setFlag(1, 'Progress Loaded')
        this.$store.commit('resetLoading')
      } catch (err) {
        log.error(err)
        this.$store.commit('resetLoading')
        this.setFlag(2, err.message)
      }
    }
  }
}
</script>

