import httpService from './httpService'
import qs from 'qs'

const drawerHandler = {
  baseUrl: '/drawer/'
}
drawerHandler.searchImages = async (args) => {
  const request = {
    url: 'searchImages',
    timeout: 3000000,
    data: qs.stringify(args),
    retry: false
  }

  const response = await httpService.post(request)
  return response.data

}

drawerHandler.saveProgress = async (args) => {
  const request = {
    url: 'saveProgress',
    timeout: 30000,
    data: qs.stringify(args),
    retry: false
  }

  const response = await httpService.post(request)
  return response.data

}

drawerHandler.loadProgress = async (projectName) => {
  const request = {
    url: `loadProgress?projectName=${projectName}`,
    retry: false
  }
  const response = await httpService.get(request)
  return response.data

}
export default drawerHandler
